#Categories {
    padding-right: rem(35);
    label {
        max-width: 100%;
        padding-bottom: 0;
        .Register-file,
        .Register-actions,
        input[type="text"] {
            height: 25px !important;
            margin: 0 !important; }
        .Register-file,
        input[type="text"] {
            background: #f7f7f7 !important;
            border: none !important; } }
    input[type="file"] {
        display: none; } }

.Empty {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: #f7f7f7;
    &.padding {
        padding: 0 3px; } }

.hidden {
    display: none !important; }

.CheckboxContainer {
    .Checkbox {
        display: flex;
        align-items: center;
        font-size: 1rem;
        text-transform: uppercase;
        height: 25px;
        input {
            display: none !important; }
        span {
            flex-grow: 1;
            height: 100%;
            background: #f7f7f7;
            padding: 3px 6px; }
        &:before {
            content: "";
            display: inline-block;
            width: rem(25);
            height: rem(25);
            border-radius: 50%;
            margin-right: 10px;
            border: #df2826 1px solid;
            cursor: pointer; } }
    &.active .Checkbox:before {
        background: #df2826; }

    &.yellow {
        &.active .Checkbox:before {
            background-color: #FFED00 !important; }
        .Checkbox:before {
            border-color: #FFED00 !important; } } }
